<template>
  <div id="login">
    <h4>用户名</h4>
    <input v-model="username" placeholder="用户名"></input>
    <h4>密码</h4>
    <input v-model="password" type="password" placeholder="密码" @keyup.enter="onLogin"></input>
    <el-button size="medium" @click="onLogin">登录</el-button>
    <p class="notice">没有账号？<router-link to="/register">注册新用户</router-link></p>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  data () {
    return {
      username: '',
      password: ''
    }
  },

  methods: {
    ...mapActions(['login']),

    onLogin () {
      this.login({ username: this.username, password: this.password })
        .then(() => {
          this.$router.push({path: this.$route.query.redirect || '/' })
        })
    }
  }
}
</script>

<style lang="less" scoped>
@import url('../../assets/base.less');

#login {
  display: grid;
  justify-content: center;
  padding-top: 60px;

  h4 {
    margin: 10px 0 5px;
  }

  p {
    margin: 5px 0;
  }

  input {
    width: 300px;
  }

  .error {
    font-size: 12px;
    color: #f00;
  }

  button {
    margin-top: 30px;
    justify-self: start;
  }

  .notice {
    font-size: 12px;
    color: @textLighterColor;
    text-align: center;
    margin-top: 30px;

    a {
      color: @themeColor;
    }
  }
}
</style>



